import React from 'react'

const Na = () => {
  return (
    <div className='screen-container'>
      <p>
        Na sekcii "Na ceste" momentlne pracujeme. Ale ke ju dokonme, toto bude to miesto
        kde bude mc sledova a povzbudi svojich kamartov, ktor sa vydaj na cestu a
        zapoja do LIVE sledovania. T o sa pripravujete na cestu v sezne 2018, svoje prpravy
        mete zaa v sekcii <a href='/pred/articles/1'>lnky</a> kde njdete kopec uitonch 
        informci, ktor sme doteraz zozbierali. Prpadne ti s plnovanm
        me pomc naa zbierka <a href='/pred/pois'>dleitch miest</a>. m skr si nahls dovolenku. 
        Mj sa bli! ;)
      </p>
    </div>
  )
}

export default Na



